.ptr-item,
a.ptr-item {
  height: 2rem;
  display: flex;
  border-radius: 0.25rem;
  border: 2px solid var(--accent30);
  background: var(--accent10);
  margin-bottom: 0.5rem;
  color: var(--accent80);
  position: relative;
}

.ptr-item.disabled,
a.ptr-item.disabled {
  pointer-events: none;
  border-color: var(--base30);
  background: var(--base10);
  color: var(--base30);
}

a.ptr-item {
  cursor: pointer;
}

.ptr-item .ptr-icon.hover:hover {
  color: var(--accent50);
}

.ptr-item:hover {
  border-color: var(--accent50);
}

.ptr-item .label {
  padding: 0.125rem 0.375rem 0;
  line-height: calc(2rem - 4px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ptr-item .option-id {
  line-height: 1rem;
  display: inline-block;
  width: 1.75rem;
  font-size: 0.618rem;
  color: var(--base40);
}

.ptr-item .ptr-item-actions-end {
  padding: 0 0.375rem;
  flex-grow: 2;
  display: flex;
  justify-content: flex-end;
}

.ptr-item .ptr-item-actions-start {
  padding: 0 0.375rem;
  display: flex;
  justify-content: flex-end;
}

.ptr-item .ptr-order-placeholder {
  width: 1rem;
  height: 1rem;
}

/* SPECIFIC OPTIONS PASSED AS PROPS */
.ptr-item.ptr-option-add {
  border-style: dashed;
  background: none;
}
.ptr-icon.ptr-inline-icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
}

.ptr-icon-baseline .ptr-icon.ptr-inline-icon {
  top: 0.2em;
  position: relative;
}

.ptr-icon-inline-wrap {
  display: inline-flex;
  align-self: center;
}
.ptr-adjustable-columns {
  display: flex;
  width: 100%;
  height: 100%;
}

.ptr-adjustable-column {
  overflow: hidden;
}

.ptr-adjustable-column-handle {
  width: 0;
  flex: 0;
  position: relative;
}

.ptr-adjustable-column-handle > div {
  width: 4px;
  position: absolute;
  left: -2px;
  top: 0;
  bottom: 0;
  z-index: 10;
  cursor: ew-resize;
}

.ptr-adjustable-columns.fixed .ptr-adjustable-column-handle {
  display: none;
}

.ptr-adjustable-columns-content {
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
}
.ptr-menu {
  overflow: hidden;
  background: #eee;
  /*box-shadow: 0 0 2px 1px rgba(0,0,0,0.3);*/
  transition: height 0.2s ease-in-out, box-shadow 0.1s 0.1s ease-in-out;
}

.ptr-menu.open {
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.3);
  transition: height 0.2s ease-in-out, box-shadow 0.1s ease-in-out;
}

.ptr-menu-item {
  height: 2rem;
  padding: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  background: var(--base10);
  transition: background 0.3s ease-in-out;
}

.ptr-menu-item.disabled {
  pointer-events: none;
  color: var(--base20);
}

.ptr-menu-item.disabled > .ptr-icon .line,
.ptr-menu-item.disabled > .ptr-icon .polygon {
  stroke: #b7b7b7;
}

.ptr-menu-item:not(:first-child) {
  border-top: 1px solid var(--base30);
}

.ptr-menu-item:hover {
  background: var(--base30);
}

.ptr-menu-item > .ptr-icon {
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  margin: 0.125rem 0.25rem;
}

.ptr-menu-item > .ptr-icon .line,
.ptr-menu-item > .ptr-icon .polygon {
  stroke: #333;
}

.ptr-menu-item-caption {
  color: var(--base95);
  display: inline-block;
  vertical-align: top;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  margin: 0 0.25rem;
}

.ptr-menu-item.disabled .ptr-menu-item-caption {
  color: var(--base30);
}
/*
	Table of contents

	Button
		General
		Icon
		Sizes (small, large, ...)
		Levels (primary, secondary, ...)
		Invisible
		Circular
		Side
		Ghost
		Inverted
		Disability

	Button menu
*/
/* === Button ======================================= */
/* General */
.ptr-button {
  background: var(--base25);
  color: var(--base90);
  border: 1px solid rgba(0, 0, 0, 0.02);
  border-radius: 0.2rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
  display: inline-block;
  min-height: 2rem;
  outline: none;
  padding: 0 0.35rem;
  position: relative;
  vertical-align: top;
  font-size: 0;
  transition: background 0.3s ease-in-out, border-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.ptr-button > * {
  position: relative;
  z-index: 1;
}

.ptr-button:after {
  content: "";
  position: absolute;
  left: -1px;
  top: -1px;
  bottom: -1px;
  right: -1px;
  background: transparent;
  transition: background 0.3s ease-in-out;
  z-index: 0;
}

.ptr-button:hover {
  color: var(--base100);
}

.ptr-button:not(.ghost):hover {
  border-color: transparent;
}

.ptr-button:not(.ghost):not(.invisible):hover:after {
  background: rgba(255, 255, 255, 0.15);
}

/* todo refactor for god's sake */
.ptr-button:not(.ghost):not(.invisible):not(.primary):not(.inverted):hover:after,
.ptr-dark .ptr-button.inverted:not(.ghost):not(.invisible):not(.primary):hover:after {
  background: rgba(255, 255, 255, 0.25);
}

.ptr-dark .ptr-button:not(.ghost):not(.invisible):not(.primary):not(.inverted):hover:after,
.ptr-button.inverted:not(.ghost):not(.invisible):not(.primary):hover:after {
  background: rgba(255, 255, 255, 0.07);
}

.ptr-button:focus::after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 0.2rem;
  box-shadow: 0 0 2px 2px var(--accent50);
  pointer-events: none;
}

.ptr-button:focus::after,
.ptr-button.inverted.primary:focus::after {
  border: 1px solid rgba(var(--base100rgb), 0.5);
}

.ptr-button.primary:focus::after,
.ptr-button.inverted:focus::after {
  border: 1px solid rgba(var(--base0rgb), 0.5);
}

.ptr-button.ghost:focus::after,
.ptr-button.invisible:focus::after {
  border: 0;
}

.ptr-button-caption {
  display: inline-block;
  vertical-align: top;
  font-size: 1rem;
  margin: 0 0.25rem;
  line-height: 1;
  padding: calc(0.55rem - 1px) 0 calc(0.45rem - 1px);
}

.ptr-button-content {
  padding: calc(0.55rem - 1px) 0 calc(0.45rem - 1px);
  margin: 0 0.25rem;
  font-size: 1rem;
}

/* Icon */
.ptr-button > .ptr-icon {
  display: inline-block;
  width: 1.25rem;
  height: 1.25rem;
  margin: calc(0.375rem - 1px) 0.25rem;
}

.ptr-button > .ptr-icon .line {
  fill: currentColor;
}

.ptr-button > .ptr-icon .polygon {
  fill: transparent;
  stroke: currentColor;
}

/* Sizes */
.ptr-button.large {
  min-height: 3rem;
  padding: 0 0.5rem;
}

.ptr-button.large:focus::after {
  box-shadow: 0 0 3px 3px var(--accent50);
}

.ptr-button.large .ptr-button-caption {
  font-size: 1.309rem;
  padding: calc(0.8455rem - 1px) 0;
  margin: 0 0.4rem;
}

.ptr-button.large > .ptr-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin: calc(0.75rem - 1px) 0.4rem;
}

.ptr-button.small {
  min-height: 1.5rem;
  padding: 0 0.35rem;
}

.ptr-button.small .ptr-button-caption {
  font-size: 0.809rem;
  padding: calc(0.4rem - 1px) 0 calc(0.291rem - 1px);
  margin: 0 0.15rem;
}

.ptr-button.small > .ptr-icon {
  width: 1rem;
  height: 1rem;
  margin: calc(0.25rem - 1px) 0.15rem;
}

/* Levels */
.ptr-button.primary {
  background: var(--accent50);
  color: var(--base0);
}

.ptr-button.primary:hover {
  color: var(--base0);
}

.ptr-button.secondary {
  background: var(--accent30);
  color: var(--base90);
}

/* Invisible */
.ptr-button.invisible,
.ptr-button.invisible.disabled {
  background: transparent;
  border-color: transparent;
  box-shadow: none;
}

.ptr-button.invisible:hover {
  background: var(--base20);
}

.ptr-button.invisible.primary {
  color: var(--accent60);
}

.ptr-button.invisible.primary:hover {
  background: var(--accent20);
}

/* Circular */
.ptr-button.circular {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  position: relative;
  top: -0.15rem;
  left: -0.15rem;
  outline: none;
  padding: 0;
}

.ptr-button.circular > .ptr-icon {
  width: 1.25rem;
  height: 1.25rem;
  margin: calc(0.625rem - 1px);
}

.ptr-button.circular {
  padding: 0;
}

.ptr-button.circular:after {
  border-radius: 50%;
}

.ptr-button.circular:focus::after {
  border-radius: 50%;
}

.ptr-button.circular.small {
  width: 2rem;
  height: 2rem;
}

.ptr-button.circular.small > .ptr-icon {
  width: 1rem;
  height: 1rem;
  margin: calc(0.5rem - 1px);
}

.ptr-button.circular.large {
  width: 3.5rem;
  height: 3.5rem;
}

.ptr-button.circular.large > .ptr-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin: calc(1rem - 1px);
}

/* Side */
.ptr-button.side {
  padding: 0;
}

.ptr-button.side-left,
.ptr-button.side-right {
  width: 1.6rem;
  height: 2.5rem;
}

.ptr-button.side-left > .ptr-icon,
.ptr-button.side-right > .ptr-icon {
  margin: calc(0.625rem - 1px) calc(0.2rem - 1px);
}

.ptr-button.side-left {
  border-radius: 0 0.2rem 0.2rem 0;
}

.ptr-button.side-right {
  border-radius: 0.2rem 0 0 0.2rem;
}

.ptr-button.side-top,
.ptr-button.side-bottom {
  height: 1.6rem;
  width: 2.5rem;
}

.ptr-button.side-top > .ptr-icon,
.ptr-button.side-bottom > .ptr-icon {
  margin: calc(0.2rem - 1px) calc(0.625rem - 1px);
}

.ptr-button.side-top {
  border-radius: 0 0 0.2rem 0.2rem;
}

.ptr-button.side-bottom {
  border-radius: 0.2rem 0.2rem 0 0;
}

/* Ghost */
.ptr-button.ghost {
  background: transparent;
  border: 1px solid var(--base60);
  box-shadow: none;
  padding: 0 calc(0.35rem - 1px);
}

.ptr-button.ghost:hover {
  background: var(--base20);
}

.ptr-button.ghost.small {
  padding: 0 calc(0.35rem - 1px);
}

.ptr-button.ghost.large {
  border-width: 2px;
  padding: 0 calc(0.5rem - 2px);
}

.ptr-button.ghost.large .ptr-button-caption {
  padding: calc(0.8455rem - 2px) 0;
}

.ptr-button.ghost.large .ptr-icon {
  margin: calc(0.75rem - 2px) 0.4rem;
}

.ptr-button.ghost.primary {
  border-color: var(--accent50);
  color: var(--accent60);
}

.ptr-button.ghost.primary:hover {
  background: var(--accent15);
}

.ptr-button.ghost.side {
  padding: 0;
}

.ptr-button.ghost.side-left {
  border-left-width: 0;
}

.ptr-button.ghost.side-right {
  border-right-width: 0;
}

.ptr-button.ghost.side-top {
  border-top-width: 0;
}

.ptr-button.ghost.side-bottom {
  border-bottom-width: 0;
}

.ptr-button.ghost.side-left > .ptr-icon,
.ptr-button.ghost.side-right > .ptr-icon {
  margin: calc(0.625rem - 1px) calc(0.2rem - 1px);
}

.ptr-button.ghost.side-top > .ptr-icon,
.ptr-button.ghost.side-bottom > .ptr-icon {
  margin: calc(0.2rem - 1px) calc(0.625rem - 1px);
}

/* Inverted */
.ptr-button.inverted {
  background: var(--base70);
  color: var(--base0);
}

.ptr-button.primary.inverted {
  background: var(--accent50);
  color: var(--base100);
}

.ptr-button.primary.inverted:hover {
  color: var(--base100);
}

.ptr-button.secondary.inverted {
  background: var(--accent70);
  color: var(--base10);
}

.ptr-button.secondary.inverted:hover {
  color: var(--base0);
}

.ptr-button.inverted.invisible {
  background: transparent;
  border-color: transparent;
  box-shadow: none;
}

.ptr-button.invisible.inverted:hover {
  background: var(--base80);
}

.ptr-button.inverted.ghost {
  border-color: var(--base30);
  background: transparent;
  box-shadow: none;
}

.ptr-button.inverted.ghost:hover {
  background: var(--base85);
}

.ptr-button.inverted.ghost.primary {
  border-color: var(--accent50);
  color: var(--accent50);
}

.ptr-button.inverted.ghost.primary:hover {
  background: var(--accent95);
}

/* Disability */
.ptr-button:not(.disabled) {
  cursor: pointer;
}

.ptr-button.disabled,
.ptr-button.primary.disabled,
.ptr-button.secondary.disabled {
  pointer-events: none;
  background: var(--base15);
  color: var(--base35);
}

.ptr-button.inverted.disabled,
.ptr-button.primary.inverted.disabled,
.ptr-button.secondary.inverted.disabled {
  background: var(--base85);
  color: var(--base65);
}

.ptr-button.ghost.disabled {
  background: transparent;
  border-color: var(--base25);
  color: var(--base35);
}

.ptr-button.disabled .ptr-icon .line {
  fill: var(--base35);
}

.ptr-button.disabled .ptr-icon .polygon {
  stroke: var(--base35);
}

/* ==== Button menu ============================ */
.ptr-button-menu {
  position: absolute;
  z-index: 2;
}

.ptr-button-menu.bottom {
  top: 100%;
}

.ptr-button-menu.top {
  bottom: 100%;
}

.ptr-button-menu.left {
  right: 0;
}

.ptr-button-menu.right {
  left: 0;
}

/* === Buttons ======================================= */
.ptr-buttons .ptr-button {
  margin-bottom: 0.5rem;
}

.ptr-buttons .ptr-button:not(:first-child),
.ptr-buttons .ptr-button-group:not(:first-child) {
  margin-left: 0.5rem;
}

.ptr-buttons.vertical {
  display: flex;
  flex-direction: column;
}

.ptr-buttons.vertical .ptr-button {
  /* todo */
  margin-left: 0;
  margin-bottom: 0;
}

.ptr-buttons.vertical .ptr-button:not(:first-child) {
  /* todo */
  margin-top: 0.5rem;
}

/* === Button group ======================================= */
/* todo */
.ptr-button-group {
  display: inline-block;
  position: relative;
}

.ptr-button-group .ptr-button,
.ptr-buttons .ptr-button-group .ptr-button {
  margin: 0;
}

.ptr-button-group .ptr-button:not(:last-child),
.ptr-button-group .ptr-button:not(:last-child):after {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.ptr-button-group .ptr-button:not(:first-child),
.ptr-button-group .ptr-button:not(:first-child):after {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.ptr-button-group .ptr-button:not(:first-child) {
  border-left: 0;
}

.ptr-button-group .ptr-button:focus {
  z-index: 2;
}

/* === Button switch ======================================= */
.ptr-button-switch .ptr-button.ghost.active {
  background: var(--accent50);
  color: var(--base10);
}

.ptr-button-switch .ptr-button.active {
  background: var(--accent50);
  color: var(--base10);
}
.ptr-center {
  display: flex;
}

.ptr-center.horizontally {
  justify-content: center;
}

.ptr-center.vertically {
  height: 100%;
  align-items: center;
}
.ptr-editable-text {
  resize: none;
  overflow: hidden;
  border: 1px solid transparent;
  background: transparent;
  padding: 0.3rem;
  font-size: 0.825rem;
  line-height: 1.5rem;
  width: 100%;
  box-sizing: border-box !important;
  /* without this fix enters infinite loop in resize() */
}

.ptr-editable-text.inverted {
  color: #ccc;
}

textarea.ptr-editable-text:focus,
textarea.ptr-editable-text.empty,
textarea.ptr-editable-text.editing {
  border: 1px dashed rgba(0, 0, 0, 0.5);
}

textarea.ptr-editable-text.inverted:focus,
textarea.ptr-editable-text.inverted.empty,
textarea.ptr-editable-text.inverted.editing {
  border-color: rgba(255, 255, 255, 0.5);
}

textarea.ptr-editable-text.empty.required {
  border-color: red;
}

.ptr-editable-text-message {
  margin: -0.1rem 0 0.5rem;
  color: #ff1e1e;
  text-align: right;
  font-size: 0.75rem;
}

.ptr-editable-text.large {
  font-size: 1.075rem;
  line-height: 1.5;
}

textarea.ptr-editable-text.invisible,
textarea.ptr-editable-text.invisible:focus {
  border: 0;
}
.ptr-fade-in-container {
  display: flex;
  flex-wrap: wrap;
}

.ptr-fade-in-container.vertical {
  flex-direction: column;
}
.ptr-input-text {
  border-radius: 0.2rem;
  min-width: 10rem;
  display: flex;
  background: none;
  border: 1px solid var(--base50);
}

.ptr-input-text.input {
  height: 2rem;
}

.ptr-input-text input {
  background: none;
  border: 0;
  border-radius: 0.2rem;
  color: var(--base100);
  width: 100%;
  font-size: 1rem;
  line-height: 1.5;
  flex: 1;
  padding: 0 0.5rem;
  outline: none;
}

.ptr-input-text input::placeholder {
  color: var(--base50);
}

.ptr-input-text.focus {
  box-shadow: 0 0 2px 1px var(--accent50);
}

/* Inverted */
.ptr-input-text.inverted input {
  background: none;
  color: var(--base0);
}

/* Multiline */
.ptr-input-text.multiline {
  min-height: 2rem;
}

.ptr-input-text.multiline > div {
  width: 100%;
}

.ptr-input-text.multiline .ptr-editable-text {
  border-radius: 0.2rem;
  color: var(--base100);
  outline: none;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  width: 100%;
}

.ptr-input-text.multiline .ptr-editable-text:focus {
  box-shadow: 0 0 2px 2px var(--accent50);
}

/* Disabled */
.ptr-input-text.disabled {
  border: 1px solid var(--base40);
  background: var(--base15);
}

.ptr-input-text.disabled input,
.ptr-input-text.disabled .ptr-editable-text {
  color: var(--base40);
}

.ptr-input-text.inverted.disabled {
  border-color: var(--base60);
  background: var(--base85);
}

.ptr-input-text.inverted.disabled input {
  color: var(--base60);
}
.ptr-input-wrapper {
  position: relative;
  border: 1px solid var(--base40);
  border-radius: 0.2rem;
  margin-bottom: 1.5rem;
  box-shadow: 0.05rem 0.05rem 0.2rem 0 rgba(0, 0, 0, 0.07);
