}

.ptr-select-container .ptr-select__control:hover {
  border-color: var(--base70);
}

.ptr-select-container .ptr-select__control--is-focused {
  box-shadow: 0 0 2px 2px var(--accent60);
}

.ptr-select-container .ptr-select__value-container {
  padding: 0 0.5rem;
  position: initial;
}

.ptr-select-container .ptr-select__single-value {
  color: var(--base100);
}

.ptr-select-container .ptr-select__placeholder {
  color: var(--base70);
}

.ptr-select-container .ptr-select__value-container > div {
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 0;
  margin-bottom: 0;
