}

.ptr-select-container .ptr-select__indicator-separator {
  background: var(--base50);
}

.ptr-select-container .ptr-select__control:hover .ptr-select__dropdown-indicator,
.ptr-select-container .ptr-select__control--is-focused .ptr-select__dropdown-indicator,
.ptr-select-container .ptr-select__dropdown-indicator:hover,
.ptr-select-container .ptr-select__clear-indicator:hover {
  color: var(--base70);
}

.ptr-select-container .ptr-select__control:hover .ptr-select__indicator-separator,
.ptr-select-container .ptr-select__control--is-focused .ptr-select__indicator-separator {
  background: var(--base70);
}

.ptr-select-container .ptr-select__control .label {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 2rem;
}

.ptr-select-container.clearable .ptr-select__control .label {
  padding-right: 4rem;
}

/* Options */
.ptr-select-container .ptr-select__menu,
.ptr-select__menu {
  margin-top: 0;
  z-index: 10;
}

.ptr-select__menu .ptr-select__menu-list {
  background-color: var(--base20);
  color: var(--base100);
  padding: 0;
  max-height: 10rem;
}

.ptr-select__menu .ptr-select__option {
  padding: 0.25rem 0.75rem;
}

.ptr-select__menu .ptr-select__option--is-focused {
  background-color: var(--accent30);
}

.ptr-select__menu .ptr-select__option--is-selected {
  background-color: var(--accent50);
}

.ptr-select__menu .ptr-select__option .label.emphasized {
  font-weight: 700;
}

/* Key prefix */
.ptr-select-container .option-id {
  display: inline-block;
  color: var(--base50);
  font-size: 0.809rem;
  width: 2.3rem;
}

.ptr-select-container .ptr-select__menu .ptr-select__option--is-selected .option-id {
  color: var(--accent20);
}

/* Multi */
.ptr-select-container.multi .ptr-select__control {
  height: auto;
}

.ptr-select-container.multi .ptr-select__value-container {
  padding: 0 0 0 0.25rem;
}

.ptr-select-container.multi .ptr-select__multi-value {
  margin: 0.15rem 0.3rem 0.15rem 0;
  background: var(--base25);
}

.ptr-select-container.multi .ptr-select__multi-value__label {
  color: var(--base80);
}

.ptr-select-container.multi .ptr-select__multi-value__remove {
  color: var(--base100);
}

.ptr-select-container.multi .ptr-select__multi-value__remove:hover {
  background: var(--accent35);
}

.ptr-select-container.clearable.multi .ptr-select__control .label {
  padding-right: 0.15rem;
}

/* Disabled */
.ptr-select-container.disabled .ptr-select__control {
  border-color: var(--base30);
}

.ptr-select-container.disabled .ptr-select__single-value {
  color: var(--base50);
}

.ptr-select-container.disabled .ptr-select__dropdown-indicator {
  color: var(--base30);
}

.ptr-select-container.disabled .ptr-select__indicator-separator {
  background: var(--base30);
}

.ptr-select-container.disabled .option-id {
  color: var(--base40);
}

/* === VALUE IS TITLE =================================================== */
.ptr-select-container.value-is-title .ptr-select__control {
  background: none;
  border: 0;
  border-bottom: 1px dashed var(--base30);
  font-size: 1.618rem;
  font-weight: 400;
  height: 2.5rem;
}

.ptr-select-container.value-is-title .ptr-select__value-container > div {
  margin-top: 0.3rem;
}

.ptr-select-container.value-is-title .ptr-select__indicator-separator {
  display: none;
}

.ptr-select-container.value-is-title .ptr-select__indicators {
  margin-top: 0.3rem;
  height: 2.5rem;
}

.ptr-select-container.value-is-title .ptr-select__control--is-focused {
  box-shadow: none;
}
/* Loading screen */
.loading-screen {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999;
  width: 100%;
  height: 100%;
  background: -webkit-radial-gradient(#c8c8c8, #969696);
  /* For Safari 5.1 to 6.0 */
  background: -o-radial-gradient(#c8c8c8, #969696);
  /* For Opera 11.6 to 12.0 */
  background: -moz-radial-gradient(#c8c8c8, #969696);
  /* For Fx 3.6 to 15 */
  background: radial-gradient(white, #e6e6e6);
  opacity: 1;
  transition: opacity 1s ease-in-out;
}

.loading-screen.transparent {
  background: radial-gradient(rgba(255, 255, 255, 0.8), rgba(230, 230, 230, 0.8));
}

.loading-screen.fade-out {
  opacity: 0;
}

.loading-screen-content-wrap {
  position: relative;
  width: 100%;
  height: 100%;
}

.loading-screen-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.6rem;
  font-weight: bold;
  color: #666;
}

/* C loader
Apache license 2.0, Copyright (c) 2015 Tom Adamec */
.a-loader-container,
.a-loader-container * {
  box-sizing: border-box;
}

.a-loader-container {
  width: 70px;
  height: 70px;
  font-size: 70px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.a-loader-container.small {
  width: 40px;
  height: 40px;
  font-size: 40px;
}

@keyframes c-loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.a-loader-container i {
  width: 1em;
  height: 1em;
  position: absolute;
  transform-origin: 50% 50%;
  border-style: solid;
  border-width: 4px;
  border-color: transparent;
  border-radius: 999%;
}

.a-loader-progress {
  font-size: 1.15rem;
  font-weight: 600;
  z-index: 1;
}

.a-loader-container i.i1 {
  animation: c-loader 1500ms linear infinite;
  border-top-color: rgba(255, 80, 60, 0.4);
  /*border-top-color: rgba(0,0,0,.2);*/
}

.a-loader-container i.i2 {
  animation: c-loader 1300ms linear infinite;
  border-top-color: rgba(50, 255, 50, 0.7);
  /*border-top-color: rgba(255,255,255,.35);*/
}

.a-loader-container i.i3 {
  animation: c-loader 1100ms linear infinite;
  border-top-color: rgba(100, 160, 255, 0.5);
  /*border-top-color: rgba(0,0,0,.3);*/
}

.a-loader-container i.i4 {
  animation: c-loader 1700ms linear infinite;
  /*border-top-color: rgba(255,255,0,.6);*/
  border-top-color: rgba(245, 245, 0, 0.6);
  /*border-top-color: rgba(255,255,255,.45);*/
}

.a-loader-container.blackandwhite i.i1 {
  border-top-color: rgba(0, 0, 0, 0.2);
}

.a-loader-container.blackandwhite i.i2 {
  border-top-color: rgba(255, 255, 255, 0.35);
}

.a-loader-container.blackandwhite i.i3 {
  border-top-color: rgba(0, 0, 0, 0.3);
}

.a-loader-container.blackandwhite i.i4 {
  border-top-color: rgba(255, 255, 255, 0.45);
}

/* C loader end */
.ptr-panther-select-current {
  display: flex;
  position: relative;
  z-index: 45;
}

.ptr-panther-select:not(.disabled):not(.currentDisabled) .ptr-panther-select-current {
  cursor: pointer;
}

.ptr-panther-select-current:focus,
.ptr-panther-select-current-icon:focus {
  outline: 0;
}

.ptr-panther-select-current > div:first-child {
  flex: 1;
  overflow: hidden;
}

.ptr-panther-select-current-icon {
  flex: 0 0 1.5rem;
}

.ptr-panther-select-current-icon > svg {
  transition: transform 0.3s ease-in-out;
}

.ptr-panther-select.open .ptr-panther-select-current-icon > svg {
  transform: rotate(180deg);
}

.ptr-panther-select-list {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 100vh;
  padding-top: 3rem;
  pointer-events: none;
  display: flex;
  z-index: 10;
}

.ptr-panther-select-list > div {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.ptr-panther-select-list > div > div {
  transform: translateY(-100%);
  transition: transform 0.3s ease-in-out;
  pointer-events: none;
  overflow: hidden;
}

.ptr-panther-select.open .ptr-panther-select-list > div > div {
  transform: none;
  pointer-events: all;
}