.ptr-popup {
  position: fixed;
  background: rgba(var(--base0rgb), 0.95);
  z-index: 99999;
  padding: 0.5rem 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.3rem;
  box-shadow: 1px 3px 7px 3px rgba(0, 0, 0, 0.2);
  font-size: 0.809rem;
  color: var(--base80);
  min-width: 8rem;
  min-height: 2rem;
}

.ptr-popup-header {
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;
}

.ptr-popup-record-group {
  display: flex;
  align-items: stretch;
  margin: 0.25rem 0 0.5rem;
}

.ptr-popup-record-group:last-child {
  margin-bottom: 0.25rem;
}

.ptr-popup-record-color {
  min-width: 0.35rem;
  margin: 0.1rem 0.5rem 0.1rem 0;
  background: var(--accent50);
}

.ptr-popup-header .ptr-popup-record-color {
  height: 0.75rem;
  width: 0.75rem;
}

.ptr-popup-record {
  flex: 1;
}

.ptr-popup-record-attribute {
  color: var(--base50);
  margin-bottom: 0.15rem;
}

.ptr-popup-record-value-group {
  line-height: 1.15;
}

.ptr-popup-record-value-group .name {
  margin-right: 0.25rem;
}

.ptr-popup-record-value-group .value {
  font-size: 1rem;
}

.ptr-popup-record-value-group .unit {
  margin-left: 0.15rem;
}

/* compressed */
.ptr-popup.compressed .ptr-popup-record {
  display: flex;
  max-width: calc(100% - 1rem);
}

.ptr-popup.compressed .ptr-popup-record-attribute {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 1;
  padding-right: 0.5rem;
}

.ptr-popup.compressed .ptr-popup-record-value-group .value {
  font-size: 0.809rem;
}
html {
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

code,
pre {
  font-family: courier new, Courier, monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: var(--base90);
  max-width: 50rem;
}

h1 {
  font-size: 2.118rem;
  font-weight: bold;
  line-height: 1.05;
  margin-bottom: 1.7239rem;
}

h2 {
  font-size: 1.618rem;
  font-weight: bold;
  line-height: 1.15;
  margin: 2.1393rem 0 1.5rem;
}

h3 {
  font-size: 1.309rem;
  font-weight: bold;
  line-height: 1.25;
  margin: 1.86375rem 0 1rem;
}

h4 {
  font-size: 1rem;
  font-weight: bold;
  margin: 1.5rem 0 1rem;
}

h5 {
  font-size: 1rem;
  font-weight: bold;
  margin-top: 1.5rem;
}

p {
  color: var(--base90);
  font-size: 1rem;
  margin-bottom: 1rem;
}

a {
  color: var(--accent50);
}

a:visited {
  color: var(--accent70);
}

a:hover {
  color: var(--accent40);
}

a:visited:hover {
  color: var(--accent55);
}

li {
  max-width: 50rem;
  color: var(--base90);
}

hr {
  border: 0;
  border-top: 1px solid #ccc;
  width: 50rem;
  margin: 1rem 0;
}

#ptr,
#ptr-app {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

